@import "https://fonts.googleapis.com/css2?family=Russo+One&display=swap";
@import "https://fonts.googleapis.com/css2?family=Bungee+Shade&display=swap";
@import "https://fonts.googleapis.com/css2?family=Roboto&display=swap";
*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --color: #e3e3e3;
}

body {
  color: var(--color);
  background: url("stars_pattern.522915a2.png"), linear-gradient(90deg, #000, #12edf042), #000;
  font-family: sans-serif;
}

.top-container {
  max-width: 1080px;
  max-height: 50vh;
  margin: 0 auto;
  padding: 1em;
  overflow: auto;
}

h1 {
  text-align: center;
  text-shadow: 3px 3px 10px #00fbff;
  margin-bottom: 1.5em;
  font-family: Bungee Shade, sans-serif;
  font-size: 3rem;
}

@media screen and (width >= 680px) {
  h1 {
    font-size: 5rem;
  }
}

.top-container > p {
  text-align: center;
  margin-bottom: 1em;
  font-family: Russo One, cursive;
  font-size: 2rem;
}

@keyframes glow-text {
  0%, 100% {
    text-shadow: var(--shadow-x) var(--shadow-y) var(--shadow-blur) var(--shadow-color);
  }

  50% {
    text-shadow: var(--shadow-x) var(--shadow-y) var(--shadow-blur) #0000;
  }
}

.top-container > p:last-child {
  --shadow-x: 0;
  --shadow-y: 0;
  --shadow-blur: .5em;
  --shadow-color: #00fbff;
  animation-name: glow-text;
  animation-duration: 3s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
}

#canvas-container {
  z-index: 0;
  background-color: #000;
  width: 100%;
  height: 50vh;
  max-height: 100vh;
  transition: height 1s ease-in-out;
  display: block;
  position: fixed;
  bottom: 0;
}

#modal {
  color: var(--color);
  border-radius: .5em;
  border-top-right-radius: initial;
  z-index: 1;
  opacity: 0;
  visibility: hidden;
  background-color: #000;
  width: 100%;
  max-width: 300px;
  font-family: Russo One, cursive;
  transition: opacity .5s ease-in-out;
  position: absolute;
  top: 1rem;
  right: 1rem;
}

#modal.active {
  opacity: 1;
  visibility: visible;
}

#modal .title {
  text-align: center;
  text-shadow: 0 0 5px #00fbff;
  margin-bottom: .8em;
}

#modal > img {
  border-top-left-radius: inherit;
  object-fit: cover;
  width: 100%;
  height: 200px;
  display: block;
}

#modal .description-field {
  margin: .5rem auto;
  font-size: .85rem;
}

#modal p b {
  color: #12edf0c4;
}

#modal > #close-btn {
  cursor: pointer;
  font-size: 1rem;
  font: inherit;
  color: var(--color);
  background-color: #000;
  border: none;
  border-radius: 4px 0 4px 4px;
  width: 100%;
  padding: .5em .6em;
  transition: box-shadow .2s ease-in-out;
  display: block;
  bottom: 0;
  box-shadow: inset 0 0 1px #00fbff;
}

#modal > #close-btn:hover {
  box-shadow: inset 0 0 42px #00fbff;
}

#modal > #modal-body {
  border-radius: inherit;
  max-height: 200px;
  padding: 1em;
  overflow: auto;
  box-shadow: inset 0 0 1px #00fbff;
}

#canvas-container > canvas {
  background-color: #000;
  width: 100%;
  height: 100%;
  display: block;
  position: absolute;
}

.controls > button#toggle-expand {
  cursor: pointer;
  border-radius: .5em;
  border-top-left-radius: initial;
  background-color: var(--color);
  border: none;
  transition: background-color .4s ease-in-out;
  display: block;
  position: absolute;
  top: 1rem;
  left: 1rem;
}

.controls > button#toggle-expand:hover {
  background-color: #12edf0;
}

@keyframes glow-box {
  0%, 100% {
    box-shadow: var(--shadow-x) var(--shadow-y) var(--shadow-blur) var(--shadow-color);
  }

  50% {
    box-shadow: var(--shadow-x) var(--shadow-y) var(--shadow-blur) #0000;
  }
}

button#toggle-expand {
  --shadow-x: .2em;
  --shadow-y: .2em;
  --shadow-blur: .5em;
  --shadow-color: #00fbff;
  box-shadow: var(--shadow-x) var(--shadow-y) var(--shadow-blur) var(--shadow-color);
  flex-direction: column;
  padding: .5em 1em;
  animation-name: glow-box;
  animation-duration: 1s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: 3;
  display: flex;
}

button#toggle-expand:before {
  content: "";
  border-top: 2px solid #000;
  border-left: 2px solid #000;
  border-radius: 3px;
  width: 10px;
  height: 10px;
  transition: transform .5s ease-in-out;
  display: block;
  position: relative;
  transform: rotate(45deg);
}

button#toggle-expand:after {
  content: "";
  border-bottom: 2px solid #000;
  border-right: 2px solid #000;
  border-radius: 3px;
  width: 10px;
  height: 10px;
  transition: transform .5s ease-in-out;
  display: block;
  position: relative;
  transform: rotate(45deg);
}

button#toggle-expand.shrink:before, button#toggle-expand.shrink:after {
  transform: rotate(-135deg);
}

.slider-fg {
  background-color: #0edee1 !important;
}

.c input[type="text"] {
  color: #0edee1 !important;
}

.dg .cr.number {
  border-left-color: #0edee1 !important;
}

